.main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main h2 {
  font-family: "Cherish Moment" !important;
  font-size: 32px;
  margin-top: 36px;
  margin-bottom: 20px;
}
.actions {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  margin-bottom: 28px;
}

.actions button:disabled {
  opacity: 0.5;
}

@media screen and (max-width: 1366px) {
  .actions {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 1170px) {
  .main {
    flex-grow: 1;
    justify-content: space-evenly;
    max-height: calc(100vh - 142px);
    overflow-y: scroll;
  }
}

@media screen and (max-width: 568px) {
  .main {
    overflow-y: scroll;
    justify-content: flex-start;
    flex-grow: initial;
    padding-bottom: 50px;
  }
  .actions {
    flex-wrap: wrap;
  }
  .actions button {
    width: calc(50% - 9px);
  }
  .main h2 {
    margin-top: 0;
  }
}

@media screen and (max-width: 468px) {
  .main {
    padding-bottom: 28px;
  }

  .main h2 {
    margin-top: 0px;
    font-size: 26px;
  }
}

@media screen and (max-width: 350px) {
  .main {
    padding-bottom: 20px;
  }
}
